:root {
  --accent: #3b82f6;
  --text-primary: #1f2937;
  --text-secondary: #6b7280;
  --text-muted: #9ca3af;
  --border-light: #e5e7eb;
  --border-focus: var(--accent);
  --bg-white: #ffffff;
  --bg-gray-50: #f9fafb;
  --bg-gray-100: #f3f4f6;
  --shadow-sm: 0 1px 2px rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px rgba(0, 0, 0, 0.1);
  --radius-sm: 4px;
  --radius-md: 6px;
  --radius-lg: 8px;
  --radius-xl: 12px;
}

body {
  background: var(--bg-gray-100);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  color: var(--text-primary);
}

.form-container {
  max-width: 480px;
  margin: 2.5rem auto;
  padding: 2rem 2rem 2.5rem 2rem;
  box-shadow: var(--shadow-md);
  border-radius: var(--radius-xl);
  background: var(--bg-white);
}

@media (max-width: 360px) {
  .form-container {
    padding: 1rem;
    margin: 0.5rem;
  }
}

.form-group {
  margin-bottom: 1.2rem;
  text-align: left;
}
label {
  display: block;
  font-size: 0.95rem;
  font-weight: 500;
  color: var(--text-secondary);
  margin-bottom: 0.22rem;
  letter-spacing: 0.01em;
}
input, textarea, select {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid var(--border-light);
  border-radius: var(--radius-md);
  font-size: 1rem;
  transition: border-color 0.18s cubic-bezier(.4,0,.2,1);
  margin-top: 0.18rem;
}
input:focus, textarea:focus, select:focus {
  outline: none;
  border-color: var(--border-focus);
  box-shadow: 0 0 0 2px rgba(59,130,246,0.07);
}
.btn-primary {
  background: var(--accent);
  color: white;
  border: 1px solid var(--accent);
  padding: 0.45rem 1.2rem;
  border-radius: var(--radius-md);
  font-weight: 500;
  cursor: pointer;
  transition: background .22s;
}
.btn-primary:hover {
  background: #2563eb;
}
.btn-secondary {
  background: white;
  color: var(--accent);
  border: 1px solid var(--accent);
  padding: 0.4rem 1.1rem;
  border-radius: var(--radius-md);
  font-weight: 500;
  cursor: pointer;
  transition: background .22s;
}
.btn-secondary:hover {
  background: var(--accent);
  color: white;
}
.error {
  color: #dc2626;
  font-size: 0.92rem;
  margin-top: 0.14rem;
}
.success {
  color: #22c55e;
  margin-top: 0.21rem;
}
.loading {
  color: var(--text-muted);
  font-style: italic;
}
.empty-state {
  text-align: center;
  color: var(--text-muted);
  font-style: italic;
  padding: 2rem 0 1rem 0;
}
.feedback-table {
  width: 100%;
  border-collapse: collapse;
  background: var(--bg-white);
  margin: 1rem 0 0 0;
  font-size: 1rem;
}
.feedback-table th,
.feedback-table td {
  border: 1px solid var(--border-light);
  padding: 0.65em .7em;
  text-align: left;
}
.feedback-table th {
  background: var(--bg-gray-50);
  font-weight: 600;
}
.fb-item {
  background: var(--bg-gray-50);
  border-radius: var(--radius-lg);
  margin-bottom: 0.85rem;
  padding: 1rem;
  border: 1px solid var(--border-light);
}
.modal {
  position: fixed;
  top: 0; left: 0; width: 100vw; height: 100vh;
  display: flex; align-items: center; justify-content: center;
  background: rgba(0,0,0,0.13);
  z-index: 999;
}
.modal-content {
  background: white;
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
  padding: 2rem 2.5rem 1.4rem 2.5rem;
  text-align: center;
}
:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  pointer-events: none;
}